import * as THREE from 'three';

const PIXEL_STEP = 10;
const LINE_HEIGHT = 40;
const PAGE_HEIGHT = 800;

export function rad2deg(rad) {
  return rad * (180 / Math.PI);
}

export function deg2rad(deg) {
  return deg * (Math.PI / 180);
}

export function getPointInBetweenByPerc(pointA, pointB, percentage) {
  let dir = pointB.clone().sub(pointA);
  const len = dir.length();

  dir = dir.normalize().multiplyScalar(len * percentage);

  return pointA.clone().add(dir);
}

export function v3(x, y, z) {
  return new THREE.Vector3(x, y, z);
}

export function v3dir(vA, vB) {
  return new THREE.Vector3().subVectors(vA, vB);
}

export function isMobile() {
  return (/iphone|ipod|android|ie|blackberry|fennec/i).test(navigator.userAgent);
}

export function pointSpline(percent, spline, upVector) {
  const position = spline.getPointAt(percent);
  const tangent = spline.getTangentAt(percent).normalize();

  const axis = new THREE.Vector3();
  const up = upVector || new THREE.Vector3(0, 0, -1);
  const radians = Math.acos(up.dot(tangent));

  axis.crossVectors(up, tangent).normalize();

  return {
    axis,
    radians,
    position,
  };
}

export function applyTransform(object) {
  if (object.children.length > 0) {
    object.children.forEach((child) => {
      child.updateMatrix();
      child.geometry.applyMatrix(child.matrix);
      child.matrix.identity();
      child.scale.set(1, 1, 1);
    });
  }
}

export function createPath(object, scale, position) {
  const mesh = new THREE.Mesh(object, new THREE.MeshStandardMaterial());
  mesh.scale.set(scale, scale, scale);

  if (position) {
    mesh.position.copy(v3(position[0], position[1], position[2]));
  }

  mesh.updateMatrix();
  mesh.geometry.applyMatrix(mesh.matrix);
  mesh.matrix.identity();
  mesh.scale.set(1, 1, 1);

  applyTransform(mesh);

  const spline = new THREE.SplineCurve3(mesh.geometry.vertices);

  const material = new THREE.LineBasicMaterial({
    color: 0xff00f0,
  });

  const geometry = new THREE.Geometry();
  const splinePoints = spline.getPoints(50);
  for (let i = 0; i < splinePoints.length; i++) {
    geometry.vertices.push(splinePoints[i]);
  }

  const line = new THREE.Line(geometry, material);

  return {
    spline,
    line,
  };
}


export function normalizeWheel(event) {
  let sX = 0;
  let sY = 0; // spinX, spinY
  let pX = 0;
  let pY = 0; // pixelX, pixelY

  // Legacy
  if ('detail' in event) {
    sY = event.detail;
  }
  if ('wheelDelta' in event) {
    sY = -event.wheelDelta / 120;
  }
  if ('wheelDeltaY' in event) {
    sY = -event.wheelDeltaY / 120;
  }
  if ('wheelDeltaX' in event) {
    sX = -event.wheelDeltaX / 120;
  }

  // side scrolling on FF with DOMMouseScroll
  if ('axis' in event && event.axis === event.HORIZONTAL_AXIS) {
    sX = sY;
    sY = 0;
  }

  pX = sX * PIXEL_STEP;
  pY = sY * PIXEL_STEP;

  if ('deltaY' in event) {
    pY = event.deltaY;
  }
  if ('deltaX' in event) {
    pX = event.deltaX;
  }

  if ((pX || pY) && event.deltaMode) {
    if (event.deltaMode === 1) { // delta in LINE units
      pX *= LINE_HEIGHT;
      pY *= LINE_HEIGHT;
    } else { // delta in PAGE units
      pX *= PAGE_HEIGHT;
      pY *= PAGE_HEIGHT;
    }
  }

  // Fall-back if spin cannot be determined
  if (pX && !sX) {
    sX = (pX < 1) ? -1 : 1;
  }
  if (pY && !sY) {
    sY = (pY < 1) ? -1 : 1;
  }

  return {
    spinX: sX,
    spinY: sY,
    pixelX: pX,
    pixelY: pY,
  };
}



