<template>
  <div class="Scene" ref="scene"></div>
</template>

<script>
  import Animation from '../common/Animation.class';

  let previousStep = 0;

  export default {
    name: 'threeDebug',
    props: ['isActive', 'isDebug', 'step', 'isStarted'],
    data() {
      return {};
    },
    watch: {
      step(val) {
        this.animation.setStep(val, true);

        if (val === 2 && previousStep === 1) {
          this.animation.switchBuildings('wroclavia');
        }

        if (val === 1 && previousStep === 2) {
          this.animation.switchBuildings('wpt');
        }

        previousStep = val;
      },
      isActive(val) {
        this.animation.setActive(val);
      },
    },
    mounted() {
      const options = {
        active: this.isActive,
        debug: this.isDebug,
        container: this.$refs.scene,
        exposeScene: false,
        // model: 'wroclavia', // TODO: debug set initial model
        step: 0, // this.step,
      };

      this.animation = new Animation(options);
      this.animation.init((p) => {
          this.$emit('progress', p);
        })
        .then(() => {
          this.$emit('loaded');
          this.animation.startTrucksAnimation();
        });
    },
  };

</script>

<style scoped>
  .Scene {
    flex: 1;
    z-index: 10;
    /*background-color: #181919;*/
    position: absolute;
    width: 100vw;
    height: 100vh;
    color: #FFF;
    align-content: center;
    justify-content: center;
    opacity: 1;
  }

  .Scene::after {
    content: "";
    background-size: cover;
    opacity: 0.25;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: 0;
  }

</style>



