const PARTIAL_BASE_WPT = 'wpt';
const PARTIAL_BASE_WROCLAVIA = 'wroclavia';

export const PARTIALS_WPT = [
  {
    model: `${PARTIAL_BASE_WPT}-buildings.json`,
    edges: true,
    wireframe: true,
    faces: false,
    mobileHidden: true,
  },
  {
    model: `${PARTIAL_BASE_WPT}-terrain.json`,
    edges: false,
    wireframe: true,
    faces: false,
    position: [0, -0.25, 0],
    mobileHidden: true,
  },
  {
    model: `${PARTIAL_BASE_WPT}-green.json`,
    edges: true,
    wireframe: false,
    faces: true,
    mobileHidden: true,
  },
  {
    model: `${PARTIAL_BASE_WPT}-building-alfa.json`,
    edges: true,
    wireframe: true,
    faces: true,
  },
  {
    model: `${PARTIAL_BASE_WPT}-roads.json`,
    edges: false,
    wireframe: true,
    faces: true,
  },
];

export const PARTIALS_WROCLAVIA = [
   {
    model: `${PARTIAL_BASE_WROCLAVIA}-buildings.json`,
    edges: true,
    wireframe: true,
    faces: true,
  },
  {
    model: `${PARTIAL_BASE_WROCLAVIA}-terrain.json`,
    edges: false,
    wireframe: true,
    faces: false,
    position: [0, 0, 0],
  },
  {
    model: `${PARTIAL_BASE_WROCLAVIA}-full.json`,
    edges: true,
    wireframe: true,
    faces: true,
  },
  {
    model: `${PARTIAL_BASE_WROCLAVIA}-roads.json`,
    edges: false,
    wireframe: true,
    faces: true,
  },
];

export const ELEMENTS_WPT = {
  truck: {
    model: `${PARTIAL_BASE_WPT}-truck.json`,
    edges: false,
    wireframe: true,
    faces: false,
    position: [0, 0, 0],
    count: 200,
  },
  path1: {
    model: `${PARTIAL_BASE_WPT}-truck-path1.json`,
    position: [0, 0.15, 0],
  },
  path2: {
    model: `${PARTIAL_BASE_WPT}-truck-path2.json`,
    position: [0, 0.15, 0],
  },
  path3: {
    model: `${PARTIAL_BASE_WPT}-truck-path3.json`,
    position: [0, 0.15, 0],
  },
};

export const ELEMENTS_WROCLAVIA = {
  bus: {
    model: `${PARTIAL_BASE_WROCLAVIA}-bus.json`,
    edges: false,
    wireframe: true,
    faces: false,
    position: [0, 0, 0],
  },
  busPath: {
    model: `${PARTIAL_BASE_WROCLAVIA}-bus-path.json`,
    position: [0, 0.9, 0],
  },
  path1: {
    model: `${PARTIAL_BASE_WROCLAVIA}-truck-path1.json`,
    position: [0, 0.15, 0],
  },
  path2: {
    model: `${PARTIAL_BASE_WROCLAVIA}-truck-path2.json`,
    position: [0, 0.15, 0],
  },
  path3: {
    model: `${PARTIAL_BASE_WROCLAVIA}-truck-path3.json`,
    position: [0, 0.15, 0],
  },
};



