<template>
  <div class="Landing" :class="['slide-' + animationDetails.currentStep, { 
      'loaded': animationDetails.isLoaded, 
      'scrolled' : animationDetails.isScrolled, 
      'scrolling' : animationDetails.isScrolling }]" ref="landing">
    <div class="background" :class="{ isOff: !animationDetails.active}">
      <Three :isStarted="loaded" :isActive="animationDetails.active" :step="animationDetails.renderStep" @loaded="onLoaded" @progress="onProgress"></Three>
      <div class="background-over"></div>
      <div class="background-over-hard"></div>
    </div>
    <div class="background-map"></div>

    <section id="slide-0">
      <h1>Przeprowadzamy si.</h1>
      <h2>lorem ipsum dolor sit amet</h2>
    </section>

    <section id="slide-1">
      <h1>hello world - slide 2</h1>
    </section>

    <section id="slide-2">
    </section>

    <section id="slide-3">
      <h1>hello world - slide 4</h1>
    </section>

    <div class="mouse">
      <div class="scroll-wheel"></div>
    </div>

    <div class="scroll">
      <p>keep scrolling...</p>
    </div>

    <div class="loader">
      <div class="dot"></div>
      <div class="dot"></div>
      <div class="dot"></div>
      <p>{{ progress }} %</p>
    </div>

    <div class="Menu" :class="{ isActive: animationDetails.currentStep > 0 }">
      <a class="logo"><span>PGS Software</span></a>
      <ul>
        <li>About</li>
        <li>Contact</li>
      </ul>
    </div>
  </div>
</template>

<script>
  /* --eslint-disable */
  import {
    throttle,
  } from 'lodash';

  import Three from './Three';

  import {
    normalizeWheel,
  } from './../common/utils';

  const scrollSettings = {
    sensivity: 9,
    duration: 600,
  };

  const touch = {
    x: 0,
    y: 0,
  };

  const animationDetails = {
    active: true,
    animatedSteps: [0, 1, 2],
    animationMap: {
      0: 0,
      1: 1,
      2: 2,
      3: 2,
    },
    currentStep: 0,
    isLoaded: false,
    isScrolled: false,
    isScrolling: false,
    renderStep: 0,
    totalSlides: 6,
  };

  let ticking = false;

  export default {
    name: 'landing',
    components: {
      Three,
    },
    data() {
      return {
        animationDetails,
        progress: 0,
        loaded: false,
        imageLoaded: false,
      };
    },
    methods: {
      onLoaded() {
        animationDetails.isLoaded = true;
      },
      onProgress(p) {
        this.progress = Math.ceil(p * 100);
      },
    },
    mounted() {
      const wheelEventName = 'onwheel' in document.createElement('div') ? 'wheel' : // Modern browsers support "wheel"
        document.onmousewheel !== undefined ? 'mousewheel' : // Webkit and IE support at least "mousewheel"
        'DOMMouseScrol'; // let's assume that remaining browsers are older Firefox
      window.addEventListener(wheelEventName, throttle(fullPageScroll, 60), false);
      window.addEventListener('touchstart', throttle(onTouchStart, 60), false);
      window.addEventListener('touchend', throttle(onTouchEnd, 60), false);
    },
  };

  function onTouchStart(e) {
    touch.x = e.touches[0].clientX;
    touch.y = e.touches[0].clientY;
  }

  function onTouchEnd(e) {
    const delta = {
      x: e.changedTouches[0].clientX - touch.x,
      y: e.changedTouches[0].clientY - touch.y,
    };

    const wheelDelta = Math.abs(delta.x) > Math.abs(delta.y) ? delta.x : delta.y;

    fullPageScroll({
      wheelDelta,
    });

    touch.x = 0;
    touch.y = 0;
  }

  function fullPageScroll(e) {
    const delta = normalizeWheel(e).pixelY;

    if (ticking !== true && animationDetails.isLoaded) {
      if (delta >= -scrollSettings.sensivity) { // nextItem
        ticking = true;
        if (animationDetails.currentStep !== animationDetails.totalSlides - 1) {
          animationDetails.currentStep++;
        }
        scrollItems();
        slideDurationTimeout(scrollSettings.duration);
      }

      if (delta <= scrollSettings.sensivity) { // prevItem
        ticking = true;
        if (animationDetails.currentStep !== 0) {
          animationDetails.currentStep--;
        }

        scrollItems();
        slideDurationTimeout(scrollSettings.duration);
      }
    }
  }

  function slideDurationTimeout(slideDuration) {
    setTimeout(() => {
      ticking = false;
    }, slideDuration);
  }

  function scrollItems() {
    animationDetails.isScrolling = true;
    setTimeout(() => {
      animationDetails.isScrolling = false; // @todo: add checking if on last step
    }, 3500);

    animationDetails.active = animationDetails.animatedSteps.reduce(
      (prev, n) => animationDetails.currentStep === n || prev, false);

    const mappedStep = animationDetails.animationMap[animationDetails.currentStep];

    if (mappedStep !== undefined) {
      animationDetails.renderStep = mappedStep;
    }

    if (!animationDetails.isScrolled) {
      animationDetails.isScrolled = true;
    }
  }

</script>

<style lang="scss" src="./Landing.scss" scoped></style>



